import styled from 'styled-components';
import { Link } from 'react-router-dom';

const DivTag = styled.div`
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 30px;
  font-weight: 500;

  & > a {
    text-decoration: none;
    color: #82b8ff;

    &:hover {
      cursor: pointer;
      color: #4a82cb;
    }
  }
`;

const MyNav = styled.nav`
  height: 55px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  background-color: #c2ddff;
  color: #1d59a7;

  & > a {
    text-decoration: none;
    color: #1d59a7;

    &:hover {
      cursor: pointer;
      color: #c2ddff;
      transition: 0.1s;
    }
  }
`;

const Home = () => {
  return (
    <>
      <br />
      <DivTag>
        <Link to="/home">HOME</Link>
      </DivTag>
      <br />
      <MyNav>
        <Link to="/board"></Link>
        |
        <Link to="/counter"> ver1</Link>
        |
        <Link to="/counterMine"> ver2</Link>
        |
        <Link to="/counterMine2"> ver3</Link>
        |
        <Link to="/proxyInstall"> </Link>
      </MyNav>
      <br /><br />
    </>
  );
};

export default Home;