import './App.css';
import './css/input.css';
import './css/table.css';
import './css/btn.css';
import CounterMine from './components/CounterMine';
import Counter from './components/Counter';
import { MyContextProvider } from './context/MyContext';
import Board from './pages/Board';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import CounterMine2 from './components/CounterMine2';
import { Provider } from 'react-redux';
import store from './redux/store';
import ProxyInstall from './pages/ProxyInstall';
import Main from './pages/Main';

function App() {
  return (
    <Provider store={store}>
      <MyContextProvider>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <Routes>
            <Route path='/counter' element={<Counter />} />
            <Route path='/counterMine2' element={<CounterMine2 />} />
            <Route path='/counterMine' element={<CounterMine />} />
            <Route path='/board' element={<Board />} />
            <Route path='/proxyInstall' element={<ProxyInstall />} />
            <Route path='*' element={<Main />} />
          </Routes>
        </BrowserRouter>
      </MyContextProvider>
    </Provider>
  );
}

export default App;