import { useEffect, useState } from "react";
import Home from "./Home";

const Board = () => {

  // url
  const url = 'http://127.0.0.1:8080/api/board';

  //  
  const [vo , setVo] = useState(
    {
      title : ''  ,
      content : '' ,
    }
  )
  
  // input , textarea   ,   
  function handleChange(evt) {
    setVo({...vo , [evt.target.name] : evt.target.value});
  }
  
  //   
  function handleSubmit(evt) {
    evt.preventDefault();

    fetch(url , {
      method : 'POST' , 
      headers : {
        "Content-Type" : "application/json" ,
      } ,
      body : JSON.stringify(vo) ,
    })
    .then( resp => resp.text() )
    .then( data => {
      //  1   
      if( data === '1'){
        alert("  .");

        //      
        fetch(url)
        .then(resp => resp.json())
        .then(voList => {
          setVoList(voList);
        });

      } else {
        alert("  .");
      }
    })
    .catch((err) => {
      console.log(err);
      alert("    .");
    });
  }

  //   
  const [voList , setVoList] = useState([]);

  useEffect(() => {
    fetch(url)
      .then(resp => resp.json())
      .then(data => {
        setVoList(data);
      })
      .catch((err) => {
        console.log(err);
        alert("     .");
      });
  }, []);

  //   
  function formatDate (timeStamp) {
    return new Date(timeStamp).toLocaleDateString();
  }

  return (
    <>
      <Home/>
      <h3> </h3>
        <form onSubmit={handleSubmit}>
          <label></label>
          <input type="text" name="title" placeholder=' .' onChange={handleChange}/>
          <br />
          <label></label>
          <textarea name="content" placeholder=' .' onChange={handleChange}></textarea>
          <br />
          <input type="submit" value='' />
        </form>
      <br />
      <h3> </h3>
      <table border={1}>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {voList.map(vo => {
            return <>
              <tr key={vo.no}>
                <td>{vo.no}</td>
                <td>{vo.title}</td>
                <td>{vo.content}</td>
                <td>{formatDate(Number(vo.createAt))}</td>
              </tr>
            </>
          })}
        </tbody>
      </table>
      <br /><br />
    </>
  );
};

export default Board;